import { Component } from './base/component';
import { createElement, ensureElement } from './../utils/utils';
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this.button = this.container.querySelector('.basket__button');
        this._total = this.container.querySelector('.basket__price');
        if (this.button)
            this.button.addEventListener('click', () => events.emit('order:open'));
        this.items = [];
    }
    //Вставляем данные в корзину
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this.button.disabled = false;
        }
        else {
            this._list.replaceChildren(createElement('p', { textContent: 'Корзина пуста', }));
        }
    }
    //Устанавливаем общую сумму товаров в корзине
    set total(total) {
        this.setText(this._total, `${total.toString()} синапсов`);
    }
}
