import { Model } from './base/Model';
class AppState extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = { address: '', payment: 'card', email: '', total: 0, phone: '', items: [] };
        this.formErrors = {};
    }
    //Вывести каталог
    setCatalog(items) {
        this.cardList = items.map(item => new CardItem(item, this.events));
        this.emitChanges('items:changed', { catalog: this.cardList });
    }
    //Вывести превью карточки
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    //Добавить товар в заказ
    addCardToBasket(item) {
        this.order.items.push(item.id);
    }
    //Вывести карточку в список окна корзины
    setCardToBasket(item) {
        this.basket.push(item);
    }
    //Вернуть список товара в корзине
    get basketList() {
        return this.basket;
    }
    //Вернуть информацию по составу в корзине
    get statusBasket() {
        return this.basket.length === 0;
    }
    //Вывести сумму заказа
    set total(value) {
        this.order.total = value;
    }
    //Вернуть общую сумму заказов
    getTotal() {
        return this.order.items.reduce((a, c) => a + this.cardList.find(it => it.id === c).price, 0);
    }
    //Удалить товар из корзины
    deleteCardToBasket(item) {
        const index = this.basket.indexOf(item);
        if (index >= 0) {
            this.basket.splice(index, 1);
        }
    }
    //Удалить товар из заказа
    deleteCardFromOrder(item) {
        const index = this.order.items.indexOf(item.id);
        if (index >= 0) {
            this.order.items.splice(index, 1);
        }
    }
    //Отчистка корзины
    clearBasket() {
        this.basket = [];
        this.order.items = [];
    }
    //Вывести данные введенные в поле доставки
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    //Вывести данные введенные в поле контакты
    setContactsField(field, value) {
        this.order[field] = value;
        if (this.validateContacts()) {
            this.events.emit('order:ready', this.order);
        }
    }
    //Валидация введенных данных
    validateOrder() {
        const errors = {};
        if (!this.order.address)
            errors.address = 'Необходимо указать адресс';
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    //Валидация введенных формы котактов
    validateContacts() {
        const errors = {};
        if (!this.order.email)
            errors.email = 'Необходимо указать email';
        if (!this.order.phone)
            errors.phone = 'Необходимо указать телефон';
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    clearOrder() {
        this.order = {
            email: '',
            phone: '',
            payment: 'cash',
            address: '',
            items: [],
            total: 0,
        };
    }
}
class CardItem extends Model {
}
export { AppState, CardItem };
